import React from 'react' 
import './index.css'
 function Nav (props){
   var btn= React.createRef();
    var btnF = React.createRef() ;
    var btnF1 = React.createRef() ;
    const repF1 = () => {
      btnF1.current.attributes.class.value= 'f' ;
      btn.current.attributes.class.value='v' ;
      console.log(btn)
   setTimeout(() => {
     props.history.push('/3')
  }, 4000)}
    const repF = () => {
      btnF.current.attributes.class.value= 'f'  ;
      btn.current.attributes.class.value='v' ;
      console.log(btn)
   setTimeout(() => {
     props.history.push('/3')
  }, 4000)}
    const rep = () => {
       btn.current.attributes.class.value= 'v'  ;
       console.log(btn)
    setTimeout(() => {
      props.history.push('/3')
   }, 4000)}
 return (
   <div className="app1">
         <a href="/" className="Large">logOut</a>
            <div className="container">
         <h1><label>50 x 4 + 5 - 2 =</label>:</h1>
         <button ref={btnF} type="button" className="faux" onClick={repF}>502</button>
         <button ref={btn} type="button" className="vrai" onClick={rep}>203</button>
         <button ref={btnF1} type="button" className="faux1" onClick={repF1}>45654</button>
      </div>
      
</div>
 )}
export default Nav ;
