import React from 'react'
import'./App.css'
import login from './login'
import Logins from './logins'
import Logins2 from './question2'
import Logins3 from './question3'
import{BrowserRouter , Route} from 'react-router-dom'
 function App () { 
return (
  <BrowserRouter>
  <Route exact path="/" component={login} />
  <Route exact path= "/1" component = {Logins} />
  <Route exact path= "/2" component = {Logins2} />
  <Route exact path= "/3" component = {Logins3} />
</BrowserRouter>
) 
}
export default App ;



