import React from 'react' 
import 'animate.css'
import './index.css'
 function Nav (props){
    var btn= React.createRef();
    var btnF = React.createRef() ;
    var btnF1 = React.createRef() ;
    const repF1 = () => {
      btnF1.current.attributes.class.value= 'f' ;
      btn.current.attributes.class.value='v' ;
   setTimeout(() => {
     props.history.push('/2')
  }, 4000)}
    const repF = () => {
      btnF.current.attributes.class.value= 'f'  ;
      btn.current.attributes.class.value='v' ;
      console.log(btn)
   setTimeout(() => {
     props.history.push('/2')
  }, 4000)}
    const rep = () => {
       btn.current.attributes.class.value= 'v'  ;
       console.log(btn)
    setTimeout(() => {
      props.history.push('/2')
   }, 4000)}
 return (
   <div className="app1">
         <a href="/" className="Large">logOut</a>
            <div className="container">
         <h1><label className="animate__backInLeft">Ny Pi dia </label>:</h1>
         <button ref={btn} id="v" type="button" onClick={rep} className="vrai">3,14</button>
         <button ref={btnF}id="f" type="button" onClick={repF}  className="faux">14,3</button>
         <button ref={btnF1}id="f" type="button" onClick={repF1}  className="faux1">31,4</button>
      </div>
      
</div>
 )}
export default Nav ;
