import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const handleLogin = () => {
    // Vérification des identifiants
    if (username === 'tii' && password === 'tii') {
      setIsLoggedIn(true);
    } else {
      alert('Nom d\'utilisateur ou mot de passe incorrect !');
    }
  };


  if (isLoggedIn) {
    // Redirection vers le tableau de bord si l'utilisateur est connecté
    return <Redirect to="/1" />;
  }

  return (
    <div>
               <div className= "table">
          <nav className="navbar navbar-inverse">
          <div className="container-fluid">
          <div className="navbar-header">
       <a className="li" href="?tii"><h1>E-lalao</h1></a>
       <a  className="link active1"href="/conn">Accueil</a>
       <a  className="link active2" href="index.php">Menu</a>
       <a  className="link active3" href="dashboard.php">A Propos</a>
     </div>
     </div>
     </nav>
     </div>
      <h2>Connexion</h2>
      <input
        type="text"
        placeholder="Nom d'utilisateur"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      /><br />
      <input
        type="password"
        placeholder="Mot de passe"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      <button className ="btn btn-danger" onClick={handleLogin}>Se connecter</button>
      <hr/>
    </div>
  );
}

export default Login;



